<template>
  <div class="page">
    <div class="page__hd">
      <h1 class="page_title">Dialog</h1>
      <p class="page_desc">WeUIVue.js</p>
    </div>
    <div class="page__bd">
      <wv-button type="default" @click.native="showDialog(1)">Dialog</wv-button>
      <wv-button type="default" @click.native="showDialog(2)">Dialog</wv-button>
    </div>
    <wv-dialog v-show="dialog1Show" type="confirm" title="" confirm-button="" cancel-button="" @wv-dialog-confirm="handleDialogAction(1)"
      @wv-dialog-cancel="handleDialogAction(1)">
      
    </wv-dialog>
    <wv-dialog v-show="dialog2Show" type="alert" title="" @wv-dialog-confirm="handleDialogAction(2)">
      
    </wv-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dialog1Show: false,
        dialog2Show: false
      }
    },
    methods: {
      showDialog(id) {
        var _this = this
        this[`dialog${id}Show`] = true
      },
      handleDialogAction(id) {
        this[`dialog${id}Show`] = false
      }
    }
  }
</script>
<style scoped>
  .page__bd {
    padding: 0 15px;
  }
</style>


